                    container.children('.collapse').children('.col-lg-12').children('.card').html(`<b> Paras tulos tähän mennessä: <p>${firstName}: ${firstBook}</b> </p> <span class="mobileShowResults"><a href="#">Näytä tulokset</a></span> tai jatka eteenpäin. </i>`)
